@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  background-color: #232324;
  color: white;
}

.spl--pt {
  padding-top: 1.5rem;
}
.spl--pb {
  padding-bottom: 1.5rem;
}
.spl--mt {
  margin-top: 1.5rem;
}
.spl--mb {
  margin-bottom: 1.5rem;
}
.spl--mb {
  margin-bottom: 1.5rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.MuiButton-root {
  background-color: hsl(199, 91%, 54%);
}

.MuiButton-root:hover {
  background-color: hsl(199, 91%, 54%);
}

.MuiButton-label {
  color: #232324;
}

.spl-root {
  max-width: 100%;
  
}

.spl-media {
  height: 7rem;
  text-align: center;
  align-self: center;
}

.spl-filterTitle {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  color: white;
}

.MuiInputBase-input {
  color: white;
}

.map-container {
  height: 80vh;
  width: 100%;
  display: block;
}
.marker {
  background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQq0U9KTY5HMWEAcylPAlJOdAR8dQI8bcharA&usqp=CAU');
  background-size: cover;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  cursor: pointer;
}
.popupdiv{
  color: #232324;
}
